// src/App.jsx
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import FileUpload from '@components/upload/FileUpload';
import Alert from '@components/Alert';
import ResultsDisplay from '@components/results/ResultsDisplay';
import { useAnalysis } from '@hooks/useAnalysis';
import { useFileUpload } from '@hooks/useFileUpload';

export default function App() {
  // ---- file upload / parsing ----
  const {
    isLoading: isUploading,
    error: fileError,
    hasFile,
    parsedContent,     // { name, size, type, text }
    uploadFile,        // function(File)
  } = useFileUpload();

  // ---- analysis ----
  const {
    results,
    isAnalyzing,
    error: analysisError,
    analyzeResume,
    clearResults,
  } = useAnalysis();

  // ---- UI state ----
  const [jobDescription, setJobDescription] = useState('');
  const [uiError, setUiError] = useState(null);
  const [showResults, setShowResults] = useState(false);

  // handlers
  const handleUpload = async (file) => {
    setUiError(null);
    setShowResults(false);
    clearResults();
    await uploadFile(file);
  };

  const handleAnalyze = async (e) => {
    e?.preventDefault();
    
    console.log(' ===== ANALYZE BUTTON CLICKED =====');
    console.log(' Resume parsed?', !!parsedContent);
    console.log(' Resume text length:', parsedContent?.text?.length || 0);
    console.log(' Job description length:', jobDescription?.length || 0);
    
    setUiError(null);
    setShowResults(false);

    const resumeText = parsedContent?.text || '';

    if (!resumeText || resumeText.trim().length < 50) {
      console.log(' ERROR: Resume too short or missing');
      const errorMsg = 'Please upload a readable resume first ( 50 characters).';
      console.log('Setting error:', errorMsg);
      setUiError(errorMsg);
      return;
    }
    
    if (!jobDescription || jobDescription.trim().length < 50) {
      console.log(' ERROR: Job description too short');
      const errorMsg = 'Please paste a fuller job description ( 50 characters).';
      console.log('Setting error:', errorMsg);
      setUiError(errorMsg);
      return;
    }

    console.log(' Validation passed! Starting analysis...');
    console.log('Calling analyzeResume with:', {
      resumeLength: resumeText.length,
      jobDescLength: jobDescription.length
    });
    
    try {
      const r = await analyzeResume(resumeText, jobDescription);
      console.log(' Analysis result:', r);
      if (r) {
        console.log(' Setting showResults to true');
        setShowResults(true);
      }
    } catch (error) {
      console.error(' Analysis error:', error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Top nav */}
      <header className="sticky top-0 z-10 bg-white/80 backdrop-blur border-b">
        <div className="max-w-6xl mx-auto px-4 py-3 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="h-9 w-9 rounded-xl bg-cyan-100 flex items-center justify-center">
              <span className="text-cyan-600 font-semibold">SR</span>
            </div>
            <div className="text-lg font-semibold">
              <span className="text-gray-800">Smart</span>
              <span className="text-cyan-600">ResumeTool</span>
            </div>
            <span className="ml-2 text-xs px-2 py-1 rounded-full bg-emerald-50 text-emerald-700 border border-emerald-200">
              Powered by AI
            </span>
          </div>
          <nav className="hidden sm:flex items-center gap-6 text-sm text-gray-600">
            <Link to="/" className="hover:text-gray-900">Home</Link>
            <Link to="/about" className="hover:text-gray-900">About</Link>
          </nav>
        </div>
      </header>

      {/* Hero */}
      <section className="max-w-6xl mx-auto px-4 pt-10 pb-6">
        <h1 className="text-4xl sm:text-5xl font-extrabold tracking-tight text-gray-900 text-center">
          Get Your Resume <span className="bg-gradient-to-r from-cyan-500 to-emerald-500 bg-clip-text text-transparent">ATS-Ready</span>
        </h1>

        <p className="mt-4 text-center text-gray-600 max-w-3xl mx-auto">
          Our AI-powered analyzer helps you optimize your resume for Applicant Tracking Systems and land more interviews.
        </p>

        {/* tiny benefits row */}
        <div className="mt-4 flex flex-wrap items-center justify-center gap-4 text-sm text-gray-700">
          <span className="flex items-center gap-2">
            <span></span> Instant AI Analysis
          </span>
          <span className="flex items-center gap-2">
            <span></span> AI-Generated Improvements
          </span>
          <span className="flex items-center gap-2">
            <span></span> Smart Keyword Matching
          </span>
        </div>
      </section>

      {/* Main cards */}
      <main className="max-w-6xl mx-auto px-4 pb-12">
        {(uiError || fileError || analysisError) && (
          <div className="mb-4">
            <Alert variant="error">
              {uiError || fileError || analysisError}
            </Alert>
          </div>
        )}

        <div className="grid md:grid-cols-2 gap-8">
          {/* Upload card */}
          <section className="bg-white rounded-2xl shadow-sm border p-6">
            <div className="flex items-center justify-between mb-3">
              <h2 className="text-lg font-semibold">Upload Resume</h2>
              <span className="text-xs text-gray-500">Step 1 of 2</span>
            </div>

            <div className="rounded-xl border-2 border-dashed border-cyan-200 bg-cyan-50/30 p-4">
              <FileUpload
                onFileUpload={handleUpload}
                isLoading={isUploading || isAnalyzing}
                error={null}
              />
            </div>

            {hasFile && parsedContent?.name && (
              <p className="mt-3 text-xs text-gray-600">
                Selected: <strong>{parsedContent.name}</strong>
                {parsedContent?.text?.length
                  ? <span>  {parsedContent.text.length.toLocaleString()} chars parsed</span>
                  : null}
              </p>
            )}
          </section>

          {/* JD card */}
          <section className="bg-white rounded-2xl shadow-sm border p-6">
            <div className="flex items-center justify-between mb-2">
              <h2 className="text-lg font-semibold">Job Description</h2>
              <span className="text-xs text-gray-500">Step 2 of 2</span>
            </div>

            <textarea
              className="w-full h-64 border rounded-lg p-3 focus:outline-none focus:ring-2 focus:ring-cyan-500/50"
              placeholder="Paste the job description here"
              value={jobDescription}
              onChange={(e) => setJobDescription(e.target.value)}
            />

            <div className="mt-2 text-xs text-gray-500">
               Pro tip: Include the entire job posting for best results.
              <span className="float-right">{jobDescription.length.toLocaleString()} chars</span>
            </div>
          </section>
        </div>

        {/* CTA */}
        <div className="mt-8 flex justify-center">
          <button
            type="button"
            onClick={handleAnalyze}
            disabled={isUploading || isAnalyzing}
            className="px-6 py-3 rounded-xl bg-gradient-to-r from-cyan-600 to-emerald-600 text-white font-semibold shadow-md hover:shadow-lg transition disabled:opacity-60"
          >
            {isAnalyzing ? 'Analyzing' : 'Analyze My Resume'}
          </button>
        </div>

        {/* Results */}
        {showResults && results && (
          <section className="mt-10">
            <ResultsDisplay 
              results={results} 
              resumeName={parsedContent?.name}
              jobTitle="Position"
            />
          </section>
        )}
      </main>
    </div>
  );
}